<template>
  <div>
    <page-title title="File Manager" />

    <v-data-table
      v-model="selected"
      :headers="headers"
      item-key="index"
      :items="files"
      :single-select="false"
      show-select
    >
      <template v-slot:[`item.ext`]="{item}">
        {{item.isDir ? 'Folder': item.ext}}
      </template>
      <template v-slot:[`item.ctime`]="{ item }" >
        {{item.ctime | timezone}}
      </template>
      <template v-slot:[`item.name`]="{item}">
        <div style="cursor: pointer" @click="() => clickName(item)">{{item.name}}</div>
      </template>
      <template v-slot:[`item.tools`]="{item}">
        <tooltip-icon-button
          btnColor="downloadColor"
          btnIcon="mdi-download"
          buttonSize="small"
          btnTooltip="Download"
          :onClick="() => clickDownload(item.name)"
        />
        <tooltip-icon-button
          btnColor="error"
          btnIcon="mdi-delete"
          buttonSize="small"
          btnTooltip="Delete"
          :onClick="() => clickDelete(item)"
        />
        <tooltip-icon-button
          btnColor="nubeGreen"
          btnIcon="mdi-rename-box"
          buttonSize="small"
          btnTooltip="Rename"
          :onClick="() => clickRename(item)"
        />
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              :loading="unZipping"
              :disabled="unZipping"
              @click="() => clickUnzip(item.name)"
              v-if="item.ext === '.zip'"
              color="primary"
              icon 
              small
               v-bind="attrs" v-on="on"
            >
              <v-icon small>mdi-package-up</v-icon>
            </v-btn>
          </template>
          <span>Unzip</span>
        </v-tooltip>
        <tooltip-icon-button
          btnColor="blue"
          btnIcon="mdi-eye-check"
          buttonSize="small"
          btnTooltip="Edit File"
          :onClick="() => clickEditFile(item)"
          v-if="!item.isDir"
        />
      </template>
      <template v-slot:top>
        <v-breadcrumbs :items="dirs" divider="/">
          <v-breadcrumbs-item
            style="cursor: pointer"
            slot="item"
            slot-scope="{ item }"
            @click.native="() => clickBreadcrumb(item)"
            :key="item.level">
            {{ item.text }}
          </v-breadcrumbs-item>
        </v-breadcrumbs>
        <div class="text-left pa-2">
          <v-btn x-small class="btn-height-22 white--text" color="error" @click="clickDeleteMulti" :disabled="selected.length === 0">Delete<v-icon x-small right>mdi-delete</v-icon></v-btn>
          <file-upload
            :headers="fileHeaders"
            :multiple="true"
            v-model="uploadFiles"
            :post-action="`${apiUrl}/file`"
            :directory="directory"
            @input-file="inputFile"
            ref="upload"
          >
          </file-upload>
          <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue-grey" @click.prevent="clickUploadFolder">Upload folder<v-icon x-small right>mdi-cloud-upload</v-icon></v-btn>
          <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue-grey" @click.prevent="clickUploadFiles">Upload files<v-icon x-small right> mdi-cloud-upload </v-icon></v-btn>
          <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue" @click="clickBackupMultiple" :disabled="selected.length === 0">Backup<v-icon x-small right>mdi-backup-restore</v-icon></v-btn>
        </div>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Are you sure?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="nubeGreen" text @click="dialog = false">No</v-btn>
          <v-btn color="red" text @click="confirm">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="renameDialog" persistent max-width="290">
      <v-card>
        <v-card-title>
          <span class="headline">Rename</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-text-field v-model="newName" label="New name" required ></v-text-field>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="renameDialog = false">Close</v-btn>
          <v-btn color="blue" text @click="rename()">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="uploadFiles.length > 0" persistent max-width="600">
      <v-card>
        <v-card-title class="headline">Upload {{uploadFiles.length}} Files</v-card-title>
        <v-card-text>
          <v-container>
            <v-data-table
              :headers="uploadHeaders"
              item-key="id"
              :items="uploadFiles"
            >
              <template v-slot:[`item.name`]="{item}">
                <div>{{item.name}}</div>
                <v-progress-linear
                  v-if="item.active || item.progress !== '0.00'"
                  v-model="item.progress"
                  color="blue-grey"
                  height="10"
                >
                  <template v-slot="{ value }">
                    <strong>{{ Math.ceil(item.progress) }}%</strong>
                  </template>
                </v-progress-linear>
              </template>
              <template v-slot:[`item.tools`]="{item}">
                <v-btn @click="() => removeUploadFile(item.id)" icon small color="error">
                  <v-icon small>mdi-delete</v-icon>
                </v-btn>
              </template>
            </v-data-table>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="uploadFiles = []">Cancel</v-btn>
          <v-btn color="nubeGreen" text :disabled="uploadFiles.length === 0" @click.prevent="$refs.upload.active = true">Upload</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="editorDialog" max-width="600">
      <v-card>
        <v-card-title>
          <span class="headline">Edit data</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-jsoneditor v-if="isJson" :options="{mode: 'code'}" v-model="fileContent" :plus="false" height="400px"></v-jsoneditor>
            <v-textarea v-if="!isJson" outlined name="input-7-4" label="import data" v-model="fileContent" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="editorDialog = false">Close</v-btn>
          <v-btn color="blue" text @click="onSaveFileContent()">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {getUrl, isBinary} from "../utils/utils";
import * as localstorageUtil from "../utils/localstorage";
import axios from "../axios";
import _axios from 'axios';
import FileUpload from 'vue-upload-component';
import VJsoneditor from 'v-jsoneditor'
import PageTitle from './Common/PageTitle.vue';
import TooltipIconButton from './RubixApps/TooltipIconButton.vue';

export default {
    components: {
      FileUpload,
      VJsoneditor,
      PageTitle,
      TooltipIconButton,
    },
    data: () => ({
        editorDialog: false,
        dialog: false,
        isJson: false,
        newName: '',
        fileContent: 'Click File name to edit',
        renameDialog: false,
        fileHeaders: {
          'Authorization': `Bearer ${localstorageUtil.getItem('token')}`,
        },
        directory: false,
        unZipping: false,
        action: '',
        currentItem: {},
        apiUrl: `${getUrl()}/api`,
        files: [],
        uploadFiles: [],
        successCount: 0,
        dirs: [{text: '...', level: 0}],
        selected: [],
        headers: [
            {
                text: 'Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            { text: 'Ext', value: 'ext' },
            { text: 'Size', value: 'size' },
            { text: 'Created Date', value: 'ctime' },
            { text: 'Tools', value: 'tools' },
        ],
        uploadHeaders: [
            {
                text: 'Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            { text: 'Size', value: 'size' },
            { text: 'Tools', value: 'tools' },
        ],
    }),
    mounted() {
        this.getFiles();
    },
    methods: {
      async clickBackupMultiple() {
        const promises = [];
        for (const file of this.selected) {
          this.currentItem = file;
          promises.push(this.backupSingle());
        }
        await Promise.all(promises);
        await this.getFiles();
        this.$toasted.show('Backup success!');
        this.selected = [];
      },
      backupSingle() {
        return axios.post(`file/backup`, {fileName: this.currentItem.name, path: this.getDir()});
      },
      onSaveFileContent() {
        const path = this.getDir() + '/' + this.currentItem.name;
        let content = this.fileContent;
        if (this.isJson) {
          content = JSON.stringify(content);
        }
        axios.post(`file/update-content`, {content: content, path}).then(r => {
          this.editorDialog = false;
          this.$toasted.show('Success!');
          this.getFiles();
        });
      },
      clickEditFile(item) {
        this.currentItem = {...item};
        const path = this.getDir() + '/' + item.name;
        axios.get(`file/get-content`, {params: { path }}).then(r => {
          if (isBinary(r)) {
            this.$toasted.show('This is not json file', {type: 'error'});
            this.editorDialog = false;
            return;
          }
          try {
            this.fileContent = JSON.parse(r);
            this.isJson = true;
          } catch (e) {
            this.fileContent = r;
            this.isJson = false;
          }
          this.editorDialog = true;
        });
      },
      async clickDownload(itemName) {
        const path = this.getDir() + '/' + itemName;
        const token = localstorageUtil.getItem('token');
        const response = await _axios.get(
          `${getUrl()}/api/file/download?path=${path}`,
          {responseType: 'blob', headers: {Authorization: 'Bearer ' + token}}
        );
        const { headers, data } = response;
        const contentDisposition = headers['content-disposition'];
        const tempFile = contentDisposition.substring(22, contentDisposition.length - 1);

        let isZip = false;
        if (tempFile.includes('temp-')) {
          isZip = true;
          const tempFilePath = this.getDir() + '/' + tempFile;
          await axios.delete(`file?path=${tempFilePath}`);
        }
        const blob = new Blob([data]);

        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.style.display = 'none';
        a.href = url;
        a.download = isZip ? `${itemName}.zip`: itemName;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
      },
      async rename() {
        if (this.newName !== '') {
          const oldPath = this.getDir() + '/' + this.currentItem.name;
          const newPath = this.getDir() + '/' + this.newName;
          await axios.post(`file/rename`, {oldPath, newPath});
          this.renameDialog = false;
          this.newName = '';
          this.getFiles();
        }
      },
      async clickUnzip(itemName) {
          this.unZipping = true;
          const path = this.getDir() + '/' + itemName;
          await axios.post(`file/un-zip?path=${path}`, {});
          this.$toasted.show('Extract file success!');
          this.unZipping = false;
          this.getFiles();
      },
      clickRename(item) {
        this.renameDialog = true;
        this.currentItem = item;
        this.newName = item.name;
      },
      removeUploadFile(fileId) {
          const result = [];
          for (let i = 0; i< this.uploadFiles.length; i++) {
              if (this.uploadFiles[i].id !== fileId) {
                  result.push(this.uploadFiles[i]);
              }
          }
          this.uploadFiles = result;
      },
      clickUploadFolder() {
          let input = this.$refs.upload.$el.querySelector('input');
          input.directory = true;
          input.webkitdirectory = true;
          this.directory = true;

          input.onclick = null;
          input.click();
          input.onclick = (e) => {
              this.directory = false;
              input.directory = false;
              input.webkitdirectory = false;
          }
      },
      clickUploadFiles() {
          let input = this.$refs.upload.$el.querySelector('input');
          input.directory = false;
          input.webkitdirectory = false;
          this.directory = false;

          input.onclick = null;
          input.click();
          input.onclick = (e) => {
              this.directory = true;
              input.directory = true;
              input.webkitdirectory = true;
          }
      },
      inputFile(newFile, oldFile, prevent) {
          if (newFile && newFile.active && !oldFile.active) {
              let dir = this.getDir();
              if (dir !== '') {
                  dir += '--';
              }
              const name = dir + newFile.name.replaceAll('/', '--');
              this.$refs.upload.update(newFile, {name})
          }
          if (newFile && oldFile) {
              if (newFile.success && !oldFile.success) {
                  this.successCount ++;
                  if (this.successCount === this.uploadFiles.length) {
                      this.uploadFiles = [];
                      this.successCount = 0;
                      this.getFiles();
                  }
              }
          }
      },
      clickBreadcrumb(dir) {
          const currentDir = this.dirs[this.dirs.length - 1];
          if (currentDir.level !== dir.level) {
              const newDirs = [];
              for (let i = 0; i <= dir.level; i++) {
                  newDirs.push(this.dirs[i]);
              }
              this.dirs = newDirs;
              this.getFiles();
          }
      },
      async clickDeleteMulti() {
          this.dialog = true;
          this.action = 'deleteSelected';
      },
      async deleteSelected() {
          const promises = [];
          for (const file of this.selected) {
              this.currentItem = file;
              promises.push(this.deleteSingle());
          }
          await Promise.all(promises);
          this.$toasted.show('Delete success!');
          this.selected = [];
      },
      async confirm() {
          await this[this.action]();
          this.dialog = false;
          this.action = '';
          this.getFiles();
      },
      async clickDelete(item) {
          this.dialog = true;
          this.action = 'deleteSingle';
          this.currentItem = item;
      },
      async deleteSingle() {
          const path = this.getDir() + '/' + this.currentItem.name;
          await axios.delete(`file?path=${path}`);
          this.$toasted.show('Delete success!');
      },
      clickName(item) {
          if (item.isDir) {
              const level = this.dirs.length;
              this.dirs.push({text: item.name, level});
              this.getFiles();
          }
      },
      getDir() {
          let dir = '';
          for (const d of this.dirs) {
              if (d.level !== 0) {
                  dir += '/' + d.text;
              }
          }
          return dir;
      },
      async getFiles() {
          const data = await axios.get(`file?dir=${this.getDir()}`)
          this.files = data.map((item, index) => ({...item, index}));
      },
    },
};
</script>
<style>
